
.menu-container {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 50%;
  transition: box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
.menu-container .menu-bar-con .menu-bar-btn {
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  z-index: 1;
  cursor: pointer;
  transition: all 0.28s cubic-bezier(0.4, 0, 0.2, 1);
  border: none;
  background-color: #74a982;
  color: #fff;
  outline: none;
}
.menu-container .menu-bar-con .menu-bar-btn.btn-active {
  transform: rotate(45deg);
}
.menu-container .menu-bar-con .menu-bar-btn:hover {
  box-shadow: 0 8px 17px 0 rgba(0,0,0,0.2);
}
.menu-container .menu-bar-con .menu-bar-btn .icon {
  font-size: 32px;
  line-height: 60%;
  position: relative;
}
.menu-container .menu-item-list {
  list-style-type: none;
}


