
body,
html {
  font-weight: 200;
}
.menu-center-wrapper {
  position: absolute;
  top: 50%;
  right: 50%;
  width: 50px;
  height: 50px;
  transform: 50% 50%;
}
.menu-left-corner-wrapper {
  position: absolute;
  bottom: 50px;
  left: 50px;
  width: 50px;
  height: 50px;
}
.item {
  opacity: 1;
  position: absolute;
  bottom: 5px;
  left: 5px;
  transition: transform 0.28s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.28s cubic-bezier(0.4, 0, 0.2, 1);
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.item .item-wrapper {
  width: 100%;
  height: 100%;
  background-color: #d8dde0;
  border-radius: 50%;
}
.item .item-wrapper .item-btn {
  cursor: pointer;
  border-radius: 50%;
  border: none;
  background-color: transparent;
  width: 100%;
  height: 100%;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  transition: box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0.28s cubic-bezier(0.4, 0, 0.2, 1);
  background-position: center center;
  background-repeat: no-repeat;
/*opacity 0.8*/
  outline: none;
}
.item .item-wrapper .item-btn:hover {
  box-shadow: 0 8px 17px 0 rgba(0,0,0,0.2);
}
.item-selected-leave-active {
  animation-name: select-item;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}
.item-not-selected-leave-active {
  animation-name: not-select-item;
  animation-duration: 0.5s;
  animation-fill-mode: backwards;
}
@keyframes select-item {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(2);
    opacity: 0;
}
}
@keyframes not-select-item {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.5);
    opacity: 0;
}
}


